*{
  margin: 0;
  padding: 0;
  border: 0;
}
.wrapper{
  height: 100vh;
  width: 100vw;
}
canvas{
  width: 100%;
  height: 100%;
}
.toolbar{
  
  position: fixed;
  width: 100%;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 0 30px 0 black;
  background-color: rgb(174, 0, 255);
}
.toolbar .btns{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40%;
  height: 100%;
}
.toolbar .btns button{
  width: 16%;
  height: 80%;
  margin: 0 2%;
  font-size: 22px;
  border-radius: 10px;
  outline: none;
  background-color: #ffffff;
  cursor: pointer;
}
.toolbar .btns .color{
  margin-left: 2%;
  width: 8%;
  cursor: pointer;
  outline: none;
}
.toolbar .btns .range{
  cursor: pointer;
}
.toolbar .btns .active{
  background-color: rgb(151, 179, 255);
  border: 3px solid black;
}
@media (max-width: 1600px) {
  .toolbar .btns{
    width: 60%;
  }
  .toolbar .btns button{
    font-size: 16;
  }
  .toolbar .btns .range {
    width: 10%;
  }
}
@media (max-width: 900px) {
  .toolbar .btns{
    width: 80%;
  }
  .toolbar .btns .range {
    width: 20%;
  }
}
@media (max-width: 500px){
  .toolbar .btns{
    width: 100%;
    height: 80%;
  }
  .toolbar .btns button{
    font-size: 16px;
  }
}